import React, { useState } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';

function Calendar() {
    const [events, setEvents] = useState([
        { id: '1', title: 'Event 1', start: '2024-08-22', description: 'This is event 1', color: 'red' },
        { id: '2', title: 'Event 2', start: '2024-08-26', description: 'This is event 2', color: 'blue' }
    ]);

    // Handle event click for editing title and description
    const handleEventClick = (clickInfo) => {
        const newTitle = prompt('Enter new event name:', clickInfo.event.title);
        const newDescription = prompt('Enter event description:', clickInfo.event.extendedProps.description);
        if (newTitle) {
            const updatedEvents = events.map(event =>
                event.id === clickInfo.event.id
                    ? { ...event, title: newTitle, description: newDescription || event.description }
                    : event
            );
            setEvents(updatedEvents);
        }
    };

    // Handle date click to add new event
    const handleDateClick = (dateClickInfo) => {
        const title = prompt('Enter event title:');
        const description = prompt('Enter event description:');
        const color = prompt('Enter event color (e.g., red, blue, green):', 'blue');
        if (title) {
            const newEvent = {
                id: String(events.length + 1),
                title,
                start: dateClickInfo.dateStr,
                allDay: dateClickInfo.allDay,
                description,
                color
            };
            setEvents([...events, newEvent]);
        }
    };

    // Handle event drop (drag and drop)
    const handleEventDrop = (dropInfo) => {
        const updatedEvents = events.map(event =>
            event.id === dropInfo.event.id
                ? { ...event, start: dropInfo.event.startStr, end: dropInfo.event.endStr }
                : event
        );
        setEvents(updatedEvents);
    };

    // Handle event resizing
    const handleEventResize = (resizeInfo) => {
        const updatedEvents = events.map(event =>
            event.id === resizeInfo.event.id
                ? { ...event, start: resizeInfo.event.startStr, end: resizeInfo.event.endStr }
                : event
        );
        setEvents(updatedEvents);
    };

    // Handle event removal
    const handleEventRemove = (removeInfo) => {
        if (window.confirm(`Are you sure you want to delete the event '${removeInfo.event.title}'?`)) {
            const updatedEvents = events.filter(event => event.id !== removeInfo.event.id);
            setEvents(updatedEvents);
        }
    };

    return (
        <div>
            <FullCalendar
                plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
                initialView="dayGridMonth"
                headerToolbar={{
                    start: 'today prev,next',
                    center: 'title',
                    end: 'dayGridMonth,timeGridWeek,timeGridDay'
                }}
                height="90vh"
                events={events}
                dateClick={handleDateClick}
                eventClick={handleEventClick}
                editable={true}
                eventDrop={handleEventDrop}
                eventResize={handleEventResize}
                eventContent={renderEventContent} // Custom rendering for events
            />
        </div>
    );
}

// Function to render custom event content including description
function renderEventContent(eventInfo) {
    return (
        <div>
            <b>{eventInfo.timeText}</b>
            <i>{eventInfo.event.title}</i>
            <div>{eventInfo.event.extendedProps.description}</div>
        </div>
    );
}

export default Calendar;
